.popup__input-error {
  color: #ff0000;
  font-size: 0.75rem;
  font-weight: normal;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  opacity: 0;
  transition: opacity .5s ease;
}

@media screen and (max-width: 500px) {
  .popup__input-error {
    font-size: 0.625rem;
  }
}
