.card__photo {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px 10px 0 0;
  -webkit-border-radius: 10px 10px 0 0;
  -moz-border-radius: 10px 10px 0 0;
  object-fit: cover;
  object-position: center;
}

.card__photo:hover {
  cursor: pointer;
}
